import React, { useState, useEffect, setState, Component } from "react";
import "./App.css";
import {
  BrowserRouter as Router,
  Route,
  Link,
  Switch,
  useHistory,
} from "react-router-dom";
import { Redirect } from "react-router";
import history from "./history";
import "semantic-ui-css/semantic.min.css";
import { Container, Header, Button, Image } from "semantic-ui-react";
import Logo from "./images/logo.png";

function App() {
  const [width, setWidth] = useState(window.innerWidth);
  const [height, setHeight] = useState(window.innerHeight);
  const updateWidthAndHeight = () => {
    setWidth(window.innerWidth);
    setHeight(window.innerHeight);
  };

  useEffect(() => {
    window.addEventListener("resize", updateWidthAndHeight);
    return () => window.removeEventListener("resize", updateWidthAndHeight);
  });

  return (
    <Container style={{ marginTop: "3em" }}>
      <Header as="h1" dividing style={{ color: "red" }}>
        <Image avatar src={Logo} />
        SASSO
      </Header>
      <Button
        fluid
        color="red"
        content="CULTURA FEMSA"
        style={{ height: height * 0.15 }}
      />
      <br />
      <Button
        fluid
        color="red"
        content="REGISTROS"
        style={{ height: height * 0.15 }}
        onClick={() => {
          history.push("/registros");
        }}
      />
      <br />
      <Button
        fluid
        color="red"
        content="ESTADISTICAS"
        style={{ height: height * 0.15 }}
      />
      <br />
      <Button
        fluid
        color="red"
        content="CONSULTAS"
        style={{ height: height * 0.15 }}
      />
    </Container>
  );
}

export default App;
